@use "mixin";

// body ==========================================

.body {
  width: 100vw;
  color: #444444;

  // =========================

  .top-page_wrapper {
    width: 100vw;
    height: auto;
    margin: 0 auto;

    /* pc 1920px以上  */
    @include mixin.pc-l(1920px) {
      width: 1920px;
      height: auto;
      margin-left: calc((100vw - 1920px) / 2); //中央揃え
    }

    // body ============================================================

    // section 1 <ファーストビュー> =======================================

    .first-view_inner {
      /* pc 1920px以上  */
      @include mixin.pc-l(1920px) {
        width: 1920px;
        height: 947px;
        position: relative;
      }

      /* PC */
      @include mixin.pc(1920px) {
        width: mixin.clampPc(1919, 1025);
        height: mixin.clampPc(947, 450);
        position: relative;
      }

      /* タブレット */
      @include mixin.tab(1025px) {
        width: 100vw;
        height: auto;
        position: relative;
      }

      /* スマートフォン */
      @include mixin.sp(600px) {
        width: 100vw;
        height: auto;
        margin-top: mixin.clampSp(10, 10);
      }

      // ================================

      &__message-left {
        font-weight: 700; //bold
        writing-mode: vertical-rl;
        display: flex;
        align-items: center;
        position: absolute;
        z-index: 5;
        background-color: white;

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          width: 102px;
          height: 565px;
          font-size: 2.8rem;
          letter-spacing: 5.5px;
          padding-top: 25px;
          top: 133px;
          left: 210px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          width: mixin.clampPc(102, 55);
          height: mixin.clampPc(565, 330);
          font-size: mixin.variableFontPc(2.8, 1.4);
          letter-spacing: 5.5px;
          padding-top: mixin.clampPc(25, 20);
          top: mixin.clampPc(133, 70);
          left: mixin.clampPc(210, 100);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          width: mixin.clampTab(50, 30);
          height: mixin.clampTab(450, 305);
          font-size: mixin.variableFontTab(2.2, 1.2);
          letter-spacing: mixin.clampTab(6, 2);
          top: mixin.clampTab(10, 10);
          left: mixin.clampTab(150, 100);
          z-index: 5;
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          width: mixin.clampSp(40, 30);
          height: mixin.clampSp(350, 280);
          font-size: mixin.variableFontSp(1.6, 1.2);
          letter-spacing: mixin.clampSp(4, 0);
          top: mixin.clampSp(10, 10);
          left: mixin.clampSp(80, 50);
          z-index: 5;
        }

        // ==============================

        span.color {
          color: #21a937;
          font-weight: 700; //bold
          display: flex;
          align-items: center;

          /* pc 1920px以上  */
          @include mixin.pc-l(1920px) {
            letter-spacing: 6.3px;
          }

          /* PC */
          @include mixin.pc(1920px) {
            letter-spacing: 6.3px;
          }

          /* タブレット */
          @include mixin.tab(1025px) {
            font-size: mixin.variableFontTab(2.2, 1.2);
            letter-spacing: mixin.clampTab(6, 2);
            margin-top: mixin.clampSp(1, 0);
            margin-bottom: mixin.clampSp(1, 0);
          }

          /* スマートフォン */
          @include mixin.sp(600px) {
            font-size: mixin.variableFontSp(1.6, 1.2);
            letter-spacing: mixin.clampSp(4, 0);
            margin-top: mixin.clampSp(1, 0);
            margin-bottom: mixin.clampSp(1, 0);
          }
        }
      }

      // =============================

      &__message-right {
        font-weight: 700; //bold
        writing-mode: vertical-rl;
        display: flex;
        align-items: center;
        position: absolute;
        z-index: 5;
        background-color: white;

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          width: 102px;
          height: 565px;
          font-size: 2.8rem;
          letter-spacing: 6px;
          padding-top: 25px;
          background-color: white;
          top: 133px;
          left: 393px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          width: mixin.clampPc(102, 55);
          height: mixin.clampPc(565, 330);
          font-size: mixin.variableFontPc(2.8, 1.4);
          letter-spacing: 5.5px;
          padding-top: mixin.clampPc(25, 20);
          background-color: white;
          top: mixin.clampPc(133, 70);
          left: mixin.clampPc(393, 200);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          width: mixin.clampTab(50, 30);
          height: mixin.clampTab(450, 305);
          font-size: mixin.variableFontTab(2.2, 1.2);
          letter-spacing: mixin.clampTab(7, 3);
          top: mixin.clampTab(10, 10);
          left: mixin.clampTab(280, 180);
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          width: mixin.clampSp(40, 30);
          height: mixin.clampSp(350, 280);
          font-size: mixin.variableFontSp(1.6, 1.2);
          letter-spacing: mixin.clampSp(5, 0);
          top: mixin.clampSp(10, 10);
          left: mixin.clampSp(150, 100);
        }
      }

      // ========================

      &__image {
        /* pc 1920px以上  */
        img {
          max-width: 100%;
          height: auto;
          /*縦横比固定*/
        }

        @include mixin.pc-l(1920px) {
          width: 1920px;
          height: 947px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          width: mixin.clampPc(1919, 1024);
          height: mixin.clampPc(947, 500);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          width: mixin.clampTab(1024, 600);
          height: auto;
        }

        @include mixin.sp(600px) {
          width: mixin.clampSp(600, 375);
          height: auto;

          img {
            width: mixin.clampSp(599, 375);
            height: mixin.clampSp(400, 300);
            object-fit: cover;
          }
        }
      }
    }
  }

  // section 1 <ファーストビュー>============================================================================================

  // section 2 <自己紹介>============================================================================================

  .self-introduction {
    margin: 0 auto;
    display: flex;
    justify-content: center;

    /* pc 1920px以上  */
    @include mixin.pc-l(1920px) {
      width: 1180px;
      height: 664px;
    }

    /* PC */
    @include mixin.pc(1920px) {
      width: mixin.clampPc(1180, 1024);
      height: mixin.clampPc(664, 400);
    }

    /* タブレット */
    @include mixin.tab(1025px) {
      width: mixin.clampTab(1024, 600);
      height: auto;
      margin-bottom: mixin.clampTab(60, 20);
    }

    /* スマートフォン */
    @include mixin.sp(600px) {
      width: 100vw;
      display: block;
    }

    // =============================

    &-inner {
      /* pc 1920px以上  */
      @include mixin.pc-l(1920px) {
        width: 490px;
        height: 420px;
      }

      /* PC */
      @include mixin.pc(1920px) {
        width: mixin.clampPc(490, 440);
        height: mixin.clampPc(420, 200);
      }

      /* タブレット */
      @include mixin.tab(1025px) {
        width: mixin.clampTab(440, 400);
        height: auto;
      }

      /* スマートフォン */
      @include mixin.sp(600px) {
        width: auto;
        text-align: center;
      }

      // ======================

      &__first-line_text {
        font-weight: 700; //bold
        display: inline-block;
        background: linear-gradient(transparent 70%, #99e5a5 70%);

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          font-size: 2.4rem;
          line-height: 1.2; //29/24
          margin-top: 195px;
          margin-left: 32px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          font-size: mixin.variableFontPc(2.4, 2);
          line-height: mixin.variableFontPc(3, 2);
          margin-top: mixin.clampPc(189, 45);
          margin-left: mixin.clampPc(32, 28);
          display: inline-block;
          height: mixin.clampPc(30, 25);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          font-size: mixin.variableFontTab(2, 1.6);
          line-height: mixin.clampSp(22, 20);
          margin-top: mixin.clampTab(120, 70);
          margin-left: 35px;
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          font-size: mixin.variableFontSp(1.6, 1.4);
          margin-top: mixin.clampSp(60, 60);
          margin-left: 0;
        }
      }

      // =======================

      &__second-line_text {
        font-weight: 700; //bold
        display: inline-block;
        background: linear-gradient(transparent 70%, #99e5a5 70%);

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          font-size: 2.4rem;
          line-height: 1.2; //29/24
          letter-spacing: -0.2px;
          margin-top: 41px;
          margin-left: 32px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          font-size: mixin.variableFontPc(2.4, 2);
          line-height: mixin.variableFontPc(3, 2);
          margin-top: mixin.clampPc(35, 15);
          margin-left: mixin.clampPc(32, 20);
          display: inline-block;
          height: mixin.clampPc(30, 25);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          font-size: mixin.variableFontTab(2, 1.6);
          line-height: mixin.clampSp(22, 20);
          margin-top: mixin.clampTab(30, 20);
          margin-left: 35px;
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          font-size: mixin.variableFontSp(1.6, 1.4);
          margin-left: 0;
        }
      }

      // ====================

      &__third-line_text {
        font-weight: 400; //regular
        display: inline-block;

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          width: auto;
          font-size: 1.6rem;
          line-height: 38px;
          letter-spacing: -0.15px;
          margin-top: 32px;
          margin-left: 32px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          font-size: mixin.variableFontPc(1.6, 1.2);
          line-height: mixin.clampPc(38, 24);
          margin-top: mixin.clampPc(35, 25);
          margin-left: mixin.clampPc(32, 28);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          font-size: mixin.variableFontTab(1.6, 1.2);
          line-height: mixin.clampTab(32, 24);
          letter-spacing: mixin.clampTab(-2, -2);
          margin-top: mixin.clampTab(45, 30);
          margin-left: 35px;
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          font-size: mixin.variableFontSp(1.2, 1.2);
          letter-spacing: 0;
          margin-left: 0;
        }
      }
    }

    // ===================================

    &__image {
      img {
        max-width: 100%;
        height: auto;
        /*縦横比固定*/
      }

      /* pc 1920px以上  */
      @include mixin.pc-l(1920px) {
        width: 690px;
        height: 420px;
        margin-top: 162px;
      }

      /* PC */
      @include mixin.pc(1920px) {
        width: mixin.clampPc(690, 360);
        height: mixin.clampPc(420, 250);
        margin-top: mixin.clampPc(162, 100);
      }

      /* タブレット */
      @include mixin.tab(1025px) {
        img {
          max-width: 85%;
          height: auto;
          /*縦横比固定*/
        }

        width: mixin.clampTab(420, 320);
        margin-top: mixin.clampTab(120, 100);
      }

      /* スマートフォン */
      @include mixin.sp(600px) {
        img {
          max-width: 100%;
          height: auto;
          /*縦横比固定*/
        }

        margin: 0 auto;
        margin-top: mixin.clampSp(80, 50);
      }
    }
  }
}

// section 2 <自己紹介> ============================================================================================

// section 3 <治療内容>============================================================================================

.medical-treatment-contents {
  background-color: #f5fcf8;
  margin: 0 auto;

  /* pc 1920px以上  */
  @include mixin.pc-l(1920px) {
    width: 1920px;
    height: auto;
    text-align: center;
    padding-bottom: 112px;
  }

  /* PC */
  @include mixin.pc(1920px) {
    width: mixin.clampPc(1919, 1025);
    height: auto;
    text-align: center;
    padding-bottom: mixin.clampPc(112, 50);
  }

  /* タブレット */
  @include mixin.tab(1025px) {
    width: 100vw;
    height: auto;
    text-align: center;
    padding-bottom: mixin.clampTab(112, 50);
  }

  /* スマートフォン */
  @include mixin.sp(600px) {
    width: 100vw;
  }

  // ===================

  &__first-line-text {
    font-weight: 700; //bold
    background: linear-gradient(transparent 70%, #99e5a5 70%);

    /* pc 1920px以上  */
    @include mixin.pc-l(1920px) {
      width: 105px;
      font-size: 2.4rem;
      line-height: 1.58; //38/24
      margin-top: 78px;
      display: inline-block;
    }

    /* PC */
    @include mixin.pc(1920px) {
      display: inline-block;
      width: auto;
      font-size: 2.4rem;
      line-height: mixin.variableFontPc(3, 2);
      margin-top: mixin.clampPc(85, 30);
    }

    /* タブレット */
    @include mixin.tab(1025px) {
      display: inline-block;
      width: auto;
      font-size: mixin.variableFontTab(2.2, 2);
      line-height: mixin.clampSp(25, 20);
      margin-top: mixin.clampTab(100, 10);
    }

    /* スマートフォン */
    @include mixin.sp(600px) {
      font-size: mixin.variableFontSp(2, 1.8);
      line-height: mixin.clampSp(22, 20);
    }
  }

  // ==============================================

  &__second-line-text {
    font-weight: 400; //regular

    /* pc 1920px以上  */
    @include mixin.pc-l(1920px) {
      width: auto;
      font-size: 1.6em;
      line-height: 22px;
      display: inline-block;
      margin-top: 68px;
    }

    /* PC */
    @include mixin.pc(1920px) {
      display: inline-block;
      width: auto;
      font-size: 1.6rem;
      line-height: 22px;
      margin-top: mixin.clampPc(79, 45);
    }

    /* タブレット */
    @include mixin.tab(1025px) {
      display: inline-block;
      width: auto;
      font-size: mixin.variableFontTab(1.6, 1.2);
      line-height: mixin.clampTab(32, 24);
      margin-top: mixin.clampTab(40, 45);
    }

    /* スマートフォン */
    @include mixin.sp(600px) {
      font-size: mixin.variableFontSp(1.5, 1.2);
    }
  }

  // ==============================================

  &__inner {
    /* pc 1920px以上  */
    @include mixin.pc-l(1920px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-top: 34px;
    }

    /* PC */
    @include mixin.pc(1920px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-top: mixin.clampPc(34, 30);
    }

    /* タブレット */
    @include mixin.tab(1025px) {
      width: 100vw;
      margin: 0;
      margin-top: mixin.clampTab(30, 20);
      flex-wrap: wrap;
    }

    /* スマートフォン */
    @include mixin.sp(600px) {
      width: 100vw;
      margin-top: mixin.clampSp(30, 15);
    }

    // =========================================

    &-article-board {
      border: 1px solid;
      border-radius: 10px;
      border-color: #c9c9c9;
      background-color: rgb(255, 255, 255);

      /* pc 1920px以上  */
      @include mixin.pc-l(1920px) {
        width: 380px;
        height: 591px;
        margin: 0 10px 0 10px;
        position: relative;
      }

      /* PC */
      @include mixin.pc(1920px) {
        width: mixin.clampPc(380, 280);
        height: mixin.clampPc(591, 350);
        margin-left: mixin.clampPc(10, 8);
        margin-right: mixin.clampPc(10, 8);
        text-align: center;
        position: relative;
      }

      /* タブレット */
      @include mixin.tab(1025px) {
        width: mixin.clampTab(300, 270);
        height: mixin.clampTab(400, 360);
        margin-top: mixin.clampPc(20, 15);
        margin-bottom: mixin.clampPc(20, 15);
      }

      /* スマートフォン */
      @include mixin.sp(600px) {
        margin: 10px 0 10px 0;
        width: 77vw;
        height: mixin.clampSp(430, 360);
      }

      // ==============================================

      &--title {
        font-weight: 700; //bold

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          font-size: 2.4rem;
          text-align: center;
          margin-top: 43px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          font-size: mixin.variableFontPc(2.4, 1.8);
          margin-top: mixin.clampPc(35, 30);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          font-size: mixin.variableFontTab(2, 1.6);
          margin-top: mixin.clampTab(35, 30);
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          font-size: mixin.variableFontSp(2, 1.6);
          letter-spacing: 1px;
        }
      }

      // ====================================================

      &--content-left {
        font-weight: 700; //bold
        margin: 0 auto;

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          width: 320px;
          font-size: 1.6rem;
          line-height: 38px;
          text-align: left;
          margin: 41px 29px 111px 30px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          max-width: 100%;
          height: auto;
          font-size: mixin.variableFontPc(1.6, 1.2);
          letter-spacing: -0.11px;
          line-height: mixin.clampPc(38, 19);
          margin-top: mixin.clampPc(42, 21);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          font-size: mixin.variableFontTab(1.2, 1.2);
          text-align: center;
          line-height: mixin.clampTab(24, 22);
          letter-spacing: -1.5px;
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          font-size: mixin.variableFontSp(1.6, 1.2);
          letter-spacing: 1px;
        }
      }

      // ==================================================

      &--content-center {
        font-weight: 700; //bold
        margin: 0 auto;

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          width: 320px;
          font-size: 1.6rem;
          line-height: 38px;
          text-align: left;
          margin: 42px 29px 111px 35px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          max-width: 100%;
          height: auto;
          font-size: mixin.variableFontPc(1.6, 1.2);
          letter-spacing: -0.11px;
          line-height: mixin.clampPc(38, 19);
          margin-top: mixin.clampPc(42, 21);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          font-size: mixin.variableFontTab(1.2, 1.2);
          text-align: center;
          line-height: mixin.clampTab(24, 22);
          letter-spacing: -1.5px;
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          font-size: mixin.variableFontSp(1.6, 1.2);
          letter-spacing: 1px;
        }
      }

      // ==================================================

      &--content-right {
        font-weight: 700; //bold
        margin: 0 auto;

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          width: 320px;
          font-size: 1.6rem;
          letter-spacing: -0.11px;
          line-height: 38px;
          text-align: left;
          margin: 41px 29px 111px 28.5px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          max-width: 100%;
          height: auto;
          font-size: mixin.variableFontPc(1.6, 1.2);
          letter-spacing: -0.11px;
          line-height: mixin.clampPc(38, 19);
          margin-top: mixin.clampPc(42, 21);
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          font-size: mixin.variableFontTab(1.2, 1.2);
          text-align: center;
          line-height: mixin.clampTab(24, 22);
          letter-spacing: -1.5px;
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          font-size: mixin.variableFontSp(1.6, 1.2);
          letter-spacing: 1px;
        }
      }

      // ==================================================

      &--image-left {
        img {
          max-width: 100%;
          height: auto;
        }

        position: absolute;
        top: 84%;
        left: 50%;
        transform: translate(-50%, -50%);

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          width: 187px;
          height: 187px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          top: 80%;
          left: 50%;
        }

        /* タブレット */
        @include mixin.tab(1025px) {
          top: 78%;
        }

        /* スマートフォン */
        @include mixin.sp(600px) {
          width: mixin.clampSp(180, 100);
          height: mixin.clampSp(180, 100);
          top: 78%;
        }
      }

      // ===================================================

      &--image-center {
        /* pc 1920px以上  */
        img {
          max-width: 100%;
          height: auto;
        }

        position: absolute;
        top: 84%;
        left: 50%;
        transform: translate(-50%, -50%);

        /* pc 1920px以上  */
        @include mixin.pc-l(1920px) {
          width: 187px;
          height: 187px;
        }

        /* PC */
        @include mixin.pc(1920px) {
          top: 80%;
          left: 50%;
